
.relogio{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
    background-color: #363636;
    width: 40%;
    border-radius: 30%;
    margin-left: auto;
    margin-right: auto; 
}

.Horario{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 40px;
    color: #FFFFFF;
    background-color: cornflowerblue;
    border-radius: 50%;
    margin-top: 10px;
    padding: 20px;
    
}


.Imagens{
    margin-top: 10px;
    padding: 10px;    
}

.Imagens img{
    width: 300px;
    height: 200px;
    border-radius: 50%;
}