.orgFooter{
    margin: 0;
    padding: 0;
    background-color: #000000;
    bottom:0;
    width:100%;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
}

.lista{
   
    display: flex; 
    justify-content: center;  
    align-items: center;
    color: #FFFFFF;
    

}

.lista div{
    text-decoration: none;
    list-style: none;
    padding: 0 50px;
}

.lista div ul{
    list-style: none;
    margin: 30px;

}